.b-product-cart-icon {
	position: relative;
	font-size: 14px;
	line-height: 1;
	font-weight: 600;
	margin-right: 24px;
	&:hover,
	&:focus {
		color: $color-dark;
		opacity: .8; }
	&__figure {
		font-size: 21px;
		margin-right: 11px; }

	&__sum {
		display: inline-block;
		vertical-align: super; }

	&__count {
		display: flex;
		align-items: center;
		justify-content: center;
		min-width: 15px;
		height: 15px;
		font-size: 10px;
		color: #ffffff;
		position: absolute;
		top: -2px;
		left: 10px;
		background-color: $color-purple;
		border: 2px solid #ffffff;
		@include border-radius(50%); } }
