.c-page {
	display: flex;
	flex-direction: column;
	height: 100%;
	overflow-x: hidden;
	&.js-mobile-menu {
		overflow: hidden; } }
.c-main {
	flex: 1 0 auto;

	&--full-height {
		height: calc(100% - 48px); } }

.c-header {
	position: relative;
	//overflow-x: hidden
	display: flex;
	align-items: center;
	flex-shrink: 0;
	padding: 8px;
	background-color: #ffffff;
	z-index: 5;
	&:after {
		content: '';
		position: absolute;
		left: 0;
		right: 0;
		top: 100%;
		display: block;
		width: 100%;
		height: 4px;
		background-image: linear-gradient(to top, rgba(230, 234, 238, 0) 0%, #e6eaee 100%); }
	&--style-2 {
		background-color: transparent;
		&:after {
			display: none; } }
	&__logo {
		position: relative;
		z-index: 10;
		padding-right: 12px; }

	&__navigation {
		padding-right: 12px;
		padding-left: 12px; }

	&__right {
		position: relative;
		z-index: 10;
		display: flex;
		align-items: center;
		margin-left: auto;
		padding-left: 12px; }

	&__button-corner {
		display: none; }

	&__burger {
		width: 48px; }
	&__create-button-mobile {
		margin: 105px -15px 0 -10px;
		text-align: center; } }

.c-content {
	padding: 50px 0 50px; }


.c-footer {
	padding: 38px 0;
	flex: 0 0 auto;
	border-top: 1px solid $color-light-bg;
	margin-left: 20px;
	margin-right: 20px;

	&__caption {
		font-size: 24px;
		font-weight: 600;
		margin-bottom: 8px; }

	&__copyright {
		border-top: 1px solid $color-light-bg;
		padding-top: 38px;
		margin-top: 38px; } }
.main-bold {
	font-weight: 600; }
.c-row {
	display: flex;
	flex-wrap: wrap;
	justify-content: center; }
.c-justify-center {
	justify-content: center; }
@media screen and (max-width: $max-lg) {

	.c-header {
		&__navigation {
			//display: none
			position: fixed;
			display: flex;
			flex-direction: column;
			justify-content: space-between;
			left: 0;
			right: 0;
			top: 0;
			bottom: 0;
			background-color: $color-accent;
			z-index: 5;
			overflow-y: auto;

			padding: 150px 48px 0;
			opacity: 0;
			visibility: hidden;
			@include scale(.2);
			@include transition(all, .4s, ease);

			&.js-active {
				opacity: 1;
				visibility: visible;
				@include scale(1); } } } }



@media screen and (min-width: $lg) {
	.c-container {
		margin-left: 100px;
		margin-right: 100px; }
	.c-header {
		&__button-corner {
			margin-top: -9px;
			display: block; }

		&__burger {
			display: none; }
		&__create-button-mobile {
			display: none; } }

	.c-footer {
		padding: 60px 0;
		margin-left: 60px;
		margin-right: 60px;
		&__row {
			display: flex;
			flex-wrap: wrap; }
		&__item {
			&--xs {
				flex: 1 0 24%;
				max-width: 24%; }
			&--lg {
				flex: 1 0 48%;
				max-width: 48%;
				margin-left: auto; } }

		&__copyright {
			display: flex;
			align-items: center;
			justify-content: space-between;
			padding-top: 60px;
			margin-top: 100px; } } }

