.b-benefit-wrap {
	padding: 69px 0 15px;
	background-color: rgba($color-light-bg, .4);
	&__item {
		flex: 1 0 100%; } }
.b-benefit {
	position: relative;
	background: transparent no-repeat center;
	background-size: contain;
	text-align: center;
	width: 210px;
	margin: 0 auto;
	padding-bottom: 0px;
	margin-bottom: 26px;

	&--blue {
		background-image: url(data:image/png;base64,);
		background-size: 75px; }
	&--yellow {
		background-image: url(data:image/png;base64,);
		background-size: 80px; }
	&--green {
		background-image: url(data:image/png;base64,);
		background-size: 75px; }
	&__figure {
		font-size: 48px;
		margin-bottom: 5px;
		&--like {
			font-size: 40px; } }
	&__title {
		font-size: 20px;
		font-weight: 600; } }

@media screen and (min-width: 768px) {
	.b-benefit-wrap {
		&__item {
			flex: 0 0 29.333%; } } }
@media screen and (min-width: $lg) {
	.b-benefit-wrap {
		padding: 132px 0 59px; }
	.b-benefit {
		background-position: bottom;
		padding-bottom: 25px;
		&--blue {
			background-size: 173px; }
		&--yellow {
			background-size: 160px; }
		&--green {
			background-size: 151px; }
		&__figure {
			font-size: 96px;
			margin-bottom: 10px;
			&--like {
				font-size: 88px; } }
		&__title {
			font-size: 24px; } } }

