.e-button {
	display: inline-block;
	font-size: 18px;
	line-height: 1;
	font-weight: 600;
	padding: 22px 30px 20px;
	background-color: $color-light-yellow;
	//background-image: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.25) 100%)
	@include border-radius(.6em);
	&:hover,
	&:focus {
		color: $color-dark; } }
.e-button-corner {
	display: inline-block;
	position: relative;
	font-size: 14px;
	font-weight: 600;

	//background-color: #f7da5f
	background-image: linear-gradient(-45deg, rgba(255,255,255,0) 8px,rgba(255,255,255,0),$color-accent 0);

	padding: 15px 17px 5px;
	border-radius: 0 0 4px 4px;

	@include transition(all, .35s, ease);

	&:before {
		content: '';
		position: absolute;
		right: 0;
		bottom: 0;

		width: 0;
		height: 0;
		border-top: 11px solid rgba(0, 0, 0, 0.2);
		border-right: 11px solid transparent; }

	&:hover,
	&:focus {
		color: $color-dark;
		opacity: .8; } }


.e-button-frame {
	display: inline-block;
	position: relative;
	font-size: 37px;
	font-weight: 300;
	padding: 5px 34px 36px 13px;
	&__border {
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		background-image: linear-gradient(-135deg, rgba(255,255,255,0) 18px,rgba(255,255,255,0),$color-dark 0);
		border-radius: 8px 8px 0 0;
		z-index: -1; }
	&:after {
		content: '';
		position: absolute;
		top: 2px;
		right: 2px;
		bottom: 0px;
		left: 2px;
		background-image: linear-gradient(-135deg, rgba(255,255,255,0) 17px,rgba(255,255,255,0),$color-accent 0);
		z-index: 1;
		border-radius: 5px 5px 0 0;
		z-index: -1; }
	&__triangle {
		position: absolute;
		top: 2px;
		right: 2px;
		width: 23px;
		height: 23px;
		background-image: url(data:image/svg+xml;base64,);
		background-position: 0px -3px;
		background-repeat: no-repeat; }
	&:hover,
	&:focus {
		color: $color-dark;
		opacity: .8; } }

.e-button-green {
	display: inline-block;
	padding: 13px 16px;
	font-size: 18px;
	font-weight: 700;
	color: #ffffff;
	border-radius: 12px;
	background-color: $color-green;
	background-image: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.15) 100%); }


@media screen and (max-width: $max-lg) {
	.e-button {
		&--tablet-full-w {
			width: 100%; } } }
@media screen and (max-width: $max-sm) {
	.e-button {
		&--mobile-full-w {
			width: 100%; } } }

@media screen and (min-width: $lg) {
	.e-button {
		font-size: 24px;
		padding: 24px 37px 22px; }
	.e-button-green {
		padding: 18px 26px; } }
