.b-notfound {
	display: flex;
	flex-wrap: wrap;
	flex-direction: column;
	//align-items: flex-end
	min-height: calc(100vh - 48px);
	padding: 100px 20px 36px;

	&__error {
		display: flex;
		align-items: flex-end;
		justify-content: center;
		font-weight: 800;
		background: transparent url(data:image/png;base64,) center no-repeat;
		background-size: contain;

		margin: 0 auto 40px;
		width: 206px;
		height: 202px;
		font-size: 90px; }

	&__text {
		font-size: 20px;
		font-weight: 400;
		margin-bottom: 20px; }
	&__item {
		&--text {
			text-align: center; }
		&--error {
			display: flex;
			align-items: flex-end;
			flex: 1; } }
	@media screen and (min-width: $lg) {
		padding: 150px 60px 36px;
		&__item--text {
			text-align: left; }
		&__text {
			font-size: 24px;
			margin-bottom: 36px; }

		&__error {
			width: 256px;
			height: 252px;
			font-size: 108px;
			padding-bottom: 17px;
			margin-bottom: 0; }
		&__item {
			&--text {
				flex: 0 0 40%;
				max-width: 40%;
				padding-right: 30px;
				margin-top: -25px; }
			&--error {
				padding-left: 30px; } } } }


