.b-reviews {
	padding: 40px 0 75px;

	&__row {
		display: flex;
		flex-wrap: wrap; }

	&__item {
		flex: 1 0 100%;
		max-width: 100%;
		&--content {
			padding-left: 44px;
			padding-right: 44px; } }
	&__caption {
		font-size: 36px;
		font-weight: 600;
		text-align: center;
		margin-bottom: 11px; }

	&__circle {
		text-align: center;
		margin-top: 25px; }
	&__circle-figure {
		font-size: 20px;
		color: $color-light-bg; }
	&__more {
		position: relative;
		display: inline-block;
		font-size: 24px;
		font-weight: 600;
		padding-right: 30px;
		margin-left: 20px;
		&:hover,
		&:focus {
			color: $color-dark; }
		&:before {
			content: '';
			position: absolute;
			top: 40%;
			right: 0;
			display: block;
			width: 73px;
			height: 65px;
			background: transparent url(data:image/png;base64,) center no-repeat;
			background-size: 73px;
			z-index: -1;
			@include translate(0, -50%); } }

	&__more-figure {
		font-size: 15px;
		margin-left: 5px; } }

.b-review {
	position: relative;
	text-align: center;
	&:before,
	&:after {
		content: '';
		display: block;
		background: transparent url(data:image/svg+xml;base64,) center no-repeat;
		-webkit-background-size: contain;
		background-size: contain;
		width: 39px;
		height: 25px;
		margin: 0 auto; }
	&:before {
		margin-bottom: 15px; }
	&:after {
		margin-top: 15px;
		@include rotate(180deg); }
	&__text {
		font-size: 24px;
		font-weight: normal;
		font-style: italic;
		margin-bottom: 10px; }
	&__twitter {
		font-size: 14px;
		opacity: .55;
		color: #262a2c; } }
.b-review-img {
	position: relative;
	display: flex;
	align-items: center;
	margin: 20px 0 30px;

	&__item {
		&:nth-child(1),
		&:nth-child(3) {
			position: absolute; }

		&:nth-child(1) {
			margin: -18px 60% 0 -7%; }
		&:nth-child(2) {
			margin: 25px 14% 0 23%; }
		&:nth-child(3) {
			margin: 34px -14% 0 67%; } } }

@media screen and (min-width: $lg) {

	.b-reviews {
		padding: 93px 60px;
		&__row {
			align-items: center; }
		&__item {
			flex: 1 0 50%;
			max-width: 50%;
			&--content {
				padding-left: 0;
				padding-right: 40px; } }

		&__caption {
			margin-bottom: 28px; }

		&__more {
			margin-left: 10%; } }

	.b-review {
		&:before {}
		margin-bottom: 18px;
		&:after {
			margin-top: 18px; } }

	.b-review-img {
		padding-top: 105px;
		margin: 20px 0 57px;
		&__item {
			&:nth-child(1) {
				margin: -57px 49% 0 17px; }
			&:nth-child(2) {
				margin: -103px 7% 0 33%; }
			&:nth-child(3) {
				margin: 58px 0% 0px 56%; } } } }
